import React from 'react'

import { useRoot } from '../atoms/RootProvider'
import { SelectControl, TSelectControlProps } from '../atoms/SelectControl'
import { FCompanyBranchBrand } from '../graph/generated'
import { useDropdownFilter } from '../hooks/useDropdownFilter'
import { i18n } from '../i18n'
import { idProp, RM } from '../tools/ramda'

type TBrand = FCompanyBranchBrand

type TProps = NoChildren &
  TSelectControlProps<TBrand> & {
    brands?: RoA<TBrand>
    selectedBrand: Maybe<TBrand>
    onSelection(brand: Maybe<TBrand>): void
  }

const brandName = RM.prop('name')

export const CompanyBrandDropdown: React.FC<TProps> = ({
  brands,
  selectedBrand,
  onSelection,
  ...selectProps
}) => {
  const { user } = useRoot()

  const filterProps = useDropdownFilter({
    getOptionFilter: brandName,
  })

  return (
    <SelectControl<TBrand>
      placeholder={i18n.t`Select brand`}
      options={brands || user.brands}
      value={selectedBrand}
      formatOptionLabel={brandName}
      getOptionValue={idProp}
      getOptionLabel={brandName}
      onChange={value => onSelection(value as Maybe<TBrand>)}
      isClearable={true}
      autoFocus={true}
      {...filterProps}
      {...selectProps}
    />
  )
}
