import React from 'react'

type TOptions<TSelectOption> = {
  getOptionFilter?(option: TSelectOption): string
}

export function useDropdownFilter<TSelectOption>({
  getOptionFilter,
}: TOptions<TSelectOption>) {
  const isSearchable = typeof getOptionFilter === 'function'

  const filterOption = React.useCallback(
    (option, rawInput) => {
      if (!getOptionFilter || rawInput.length === 0) {
        return true
      }
      return getOptionFilter(option.data)
        .toLocaleLowerCase()
        .includes(rawInput.toLocaleLowerCase())
    },
    [getOptionFilter],
  )

  return { filterOption, isSearchable }
}
