import { useRoot } from '../../../atoms/RootProvider'
import { useQZoneList } from '../../../graph/generated'
import { idProp } from '../../../tools/ramda'

export function useZoneListQuery() {
  const { zone } = useRoot()
  const branches = zone.branches.filter(zone.branchFilterPredicate).map(idProp)
  const skip = branches.length === 0

  const { getStash, loading } = useQZoneList({ branches }, { skip })

  const zones = getStash(data => data.zones)

  return { zones, loading }
}
